
.collapse-list[data-v-047eb019] {
}
.collapse-list .select-list[data-v-047eb019] {
}
.collapse-list .list-item .collapse[data-v-047eb019] {
  padding-left: 20px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 34px;
}
.collapse-list .list-item .carte-ico[data-v-047eb019] {
  padding-right: 10px;

  transform: scale(.83333333) rotate(0deg);
}
.collapse-list .list-item .carte-ico[data-v-047eb019] svg {
  transition: transform .3s;
  transform: rotate(-90deg);
}
.collapse-list .list-item .carte-ico.ico_expanded[data-v-047eb019] svg {
  transform: rotate(0deg);
}
.collapse-list .sub-list .sub-item-text[data-v-047eb019] {
  padding-left: 40px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  padding-right: 34px;
}
.collapse-list .select-list .item-text[data-v-047eb019] {
  cursor: pointer;
  margin: 0;
  height: 44px;
  line-height: 44px;
}
.collapse-list .select-list .item-text.even-row[data-v-047eb019] {
  background: #f6f6f6;
}
.collapse-list .select-list .item-text[data-v-047eb019]:hover {
  background: #e6f7ff;
}
.collapse-list .select-list .item-text .no-ico[data-v-047eb019] {
  padding-left: 24px;
}
.collapse-list[data-v-047eb019] .ant-checkbox {
  top: 0;
}
.collapse-list[data-v-047eb019] .ant-checkbox-wrapper {
  display: inline-block;
  line-height: 44px;
  height: 44px;
  width: 100%;
}
.collapse-list[data-v-047eb019] .ant-checkbox + span {
  white-space: nowrap;
  text-overflow: ellipsis;
  width: calc(100% - 26px);
  overflow: hidden;
  display: inline-block;
  vertical-align: middle;
}

